*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.main{
  margin: 30px auto 0 auto;
  width: 300px;
  max-width: 100%;
  box-shadow: 0 0 10px grey;
  padding: 20px;
  border-radius: 10px;
}
.form input{
  width: 100%;
  padding: 10px 0 10px 10px;
  margin-bottom: 5px;
}
.form button{
  width: 100%;
  padding: 10px 0;
  background-color: #1dbf73;
  border: 1px solid #fff;
  color: #fff;
  font-size: 18px;
  cursor: pointer;
}
.imgCol{
  text-align: center;
}
.imgCol img{
  width: 150px;
  border-radius: 100%;
}
.follower{
  display: flex;
  justify-content: space-around;
  box-shadow: 0 0 10px grey;
  padding: 10px;
  margin: 10px 0;
}
.follower p{
  font-size: 18px;
}